<template>
    <div>
        <keep-alive>
            <router-view></router-view>
        </keep-alive>
    </div>
</template>

<script>
export default {
    name: 'PageFrame',
    methods: {
        changePage() {

        }
    },
    mounted() {
        let url = document.location.href.split('/');
        let target = url[url.length - 1];
        if (target != '') {
            this.$router.push({
                name: target
            })
        }
    }
}
</script>

