<template>
    <div class="frame">
        <div class="title">服务器守则</div>
        <div class="subtitle">请您详细阅读并遵守服务器守则</div>

        <el-collapse>

            <el-collapse-item v-for="chapter in ruleData" :key="chapter.index"
                :title="chapter.index + '、 ' + chapter.title" :name="chapter.index">

                <div class="desc">{{ chapter.subtitle }}</div>

                <div v-for="item in chapter.item" :key="item">
                    <div class="item">{{ item }}</div>
                </div>

            </el-collapse-item>

        </el-collapse>

        <div class="subtitle">免责声明</div>
        <el-collapse>
            <el-collapse-item v-for="claim in claims" :key="claim.index" :title="claim.index + '、 ' + claim.title"
                :name="claim.index">

                <div>{{ claim.desc }}</div>
            </el-collapse-item>
        </el-collapse>


        <div class="subtitle">处罚记录</div>

        <el-table class="history" :data="history" stripe border>
            <el-table-column prop="date" label="日期" min-width="2"></el-table-column>
            <el-table-column prop="name" label="玩家名称" min-width="2"></el-table-column>
            <el-table-column prop="type" label="类型" min-width="1"></el-table-column>
            <el-table-column prop="time" label="时长" min-width="1"></el-table-column>
            <el-table-column prop="reason" label="原因" min-width="6"></el-table-column>
        </el-table>

    </div>
</template>

<script>
export default {
    name: 'RulePage',
    data() {
        return {
            ruleData: {},
            claims: [],
            history: []
        }
    },
    methods: {
        initData() {
            let rawJson = require("../../../public/static/ruleData.json");
            this.ruleData = rawJson.chapters;

            let claimJson = require("../../../public/static/claim.json");
            this.claims = claimJson.data;

            let historyJson = require("../../../public/static/history.json");
            this.history = historyJson.data;
        }
    },
    mounted() {
        this.initData();
    }
}
</script>

<style scoped>
.frame {
    width: 70%;
    margin-left: 15%;
}

.title {
    font-size: 70px;
    font-weight: bold;
}

.subtitle {
    font-size: 50px;
    margin-top: 20px;
    margin-bottom: 20px;
}

.desc {
    font-size: 40px;
}

.item {
    text-align: left;
    font-size: 25px;
}

.history {
    margin-top: 30px;
    margin-bottom: 30px;
}
</style>